* {
  box-sizing: border-box;
  clear: both;
  outline: none;
  margin: 0;
  padding: 0;
}

a, a:hover, a:focus, a:active {
  color: #000000e6;
  text-decoration: none;
}

p {
  font-size: 16px;
  line-height: 25px;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

main {
  border: 1px solid #000000e6;
  border-radius: 20px;
  width: 80%;
  margin-inline: auto;
  padding: 40px 40px 0;
}

#header {
  border-bottom: 2px solid #000;
  flex-flow: wrap;
  place-content: flex-start space-between;
  align-items: flex-start;
  padding-bottom: 10px;
  display: flex;
}

#header h1 {
  margin: 0;
  font-size: 36px;
}

#contact ul {
  list-style-type: none;
}

#contact ul li {
  margin-bottom: 5px;
}

#context_box {
  flex-wrap: wrap;
  place-content: flex-start;
  align-items: flex-start;
  display: flex;
}

#skills_others {
  width: 30%;
  padding: 20px 20px 20px 0;
}

#experience_section {
  border-left: 2px solid #000;
  width: 70%;
  padding: 20px;
}

.title {
  margin-bottom: 10px;
  font-size: 24px;
}

.skills_others_box, .tools_others_box {
  margin-bottom: 20px;
}

.mb-10 {
  margin-bottom: 10px !important;
}

.mb-20 {
  margin-bottom: 20px !important;
}

#soft_skills .skill_list li, #skills .skill_list li, #tools .tool_list li {
  color: #fff;
  background: #222222d9;
  border-radius: 10px;
  margin-bottom: 10px;
  padding: 10px 20px;
  display: inline-block;
}

.experience .title {
  margin-bottom: 0;
}

.exp-1, .exp-2, .exp-3 {
  margin-bottom: 10px;
}

.company {
  font-size: 20px;
}

.exp-1 .title, .exp-2 .title, .exp-3 .title {
  color: #555;
  font-size: 18px;
  font-weight: normal;
}

.position_highlights {
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 20px;
  list-style-type: disc;
}

.position_highlights li {
  margin-bottom: 10px;
  font-size: 16px;
}

.icons {
  width: 20px;
  height: 20px;
  margin-right: 5px;
  display: inline-block;
  position: relative;
  top: 3px;
  overflow: hidden;
}

.icons img {
  height: 100%;
}

@media print {
  .download_cv {
    display: none;
  }

  main {
    border: none;
    width: 100%;
    padding: 0;
  }

  #soft_skills .skill_list li, #skills .skill_list li, #tools .tool_list li {
    color: #222;
    margin-right: 5px;
    padding: 0;
    font-size: 16px;
    font-weight: bolder;
    display: inline-block;
    position: relative;
  }

  #soft_skills .skill_list li:after, #skills .skill_list li:after, #tools .tool_list li:after {
    content: ",";
    position: absolute;
  }
}

@media only screen and (width <= 920px) {
  main {
    border: 0;
    width: 100%;
    margin: 0;
    padding: 15px;
  }
}

@media only screen and (width <= 760px) {
  main {
    border: 0;
    width: 100%;
    margin: 0;
    padding: 15px;
  }

  #context_box {
    flex-direction: column-reverse;
  }

  #skills_others, #experience_section {
    border-top: 2px solid #000;
    width: 100%;
    padding: 20px 0 0;
  }

  #experience_section {
    border: none;
  }

  #header h1 {
    font-size: 24px;
  }

  .title {
    font-size: 20px;
  }

  #soft_skills .skill_list li, #skills .skill_list li, #tools .tool_list li {
    padding: 10px 15px;
    font-size: 14px;
  }

  .exp-1 .title, .exp-2 .title, .exp-3 .title, .company {
    font-size: 16px;
  }

  .position_highlights li, p {
    font-size: 14px;
  }
}
